import React from 'react';
import { Link } from 'react-router-dom';

import '../assets/styles/Footer.css';
import heart from '../assets/static/heart.png';
import twIcon from '../assets/static/tw-icon.png';
import igIcon from '../assets/static/ig-icon.png';
import linkedinIcon from '../assets/static/linkedin-icon.png';
import githubIcon from '../assets/static/github-icon.png';

const Footer = () => {
  return (
    <div className='footer'>
      <h5 className='footer-title'>
        Made with <img src={heart} alt='pokeball heart' /> by Pedro Silva
      </h5>
      <div className='footer-links'>
        <a
          href='https://www.linkedin.com/in/pedro-alejandro-silva-bab42b18a/'
          className=' text-decoration-none'
          target='_blank'
          rel='noreferrer'
        >
          <img
            className='footer--icons'
            src={linkedinIcon}
            alt='linkedIn icon'
            rel='noreferrer'
            target='_blank'
          />
        </a>
        <a
          href='https://github.com/pedroalesp'
          className=' text-decoration-none'
          alt='linkedIn icon'
          rel='noreferrer'
          target='_blank'
        >
          <img className='footer--icons' src={githubIcon} alt='github icon' />
        </a>
        <a
          href='https://www.instagram.com/pedroalej_/'
          className=' text-decoration-none'
          alt='linkedIn icon'
          rel='noreferrer'
          target='_blank'
        >
          <img className='footer--icons' src={igIcon} alt='instagram icon' />
        </a>
        <a
          href='https://twitter.com/pedroale_sp'
          className=' text-decoration-none'
          alt='linkedIn icon'
          rel='noreferrer'
          target='_blank'
        >
          <img className='footer--icons' src={twIcon} alt='twitter icon' />
        </a>
      </div>
    </div>
  );
};

export default Footer;
